const DAYS_OF_THE_WEEK = [
    "Domenica",
    "Luned",
    "Marted",
    "Mercoled",
    "Gioved",
    "Venerd",
    "Sabato",
    "Domenica",
];

const MONTH_OF_THE_YEAR = [
    "Gennaio",
    "Febbraio",
    "Marzo",
    "Aprile",
    "Maggio",
    "Giugno",
    "Luglio",
    "Agosto",
    "Settembre",
    "Ottobre",
    "Novembre",
    "Dicembre",
];

const getDayName = (input: Date, mode: "short" | "long" = "short") => {
    let output = "";
    if (mode === "long")
        output = output + DAYS_OF_THE_WEEK[input.getDay()] + " ";
    return (
        output +
        `${input.getDate()}${
            mode === "short"
                ? `/${input.getMonth() + 1}/`
                : ` ${MONTH_OF_THE_YEAR[input.getMonth()]} `
        }${input.getFullYear()}`
    );
};

export default getDayName;
